@font-face {
  font-family: Epilogue;
  src: local(Epilogue Light), local(Epilogue-Light), url("Epilogue-Light.5f1f0a5f.woff2") format("woff2"), url("Epilogue-Light.e0503ba4.woff") format("woff"), url("Epilogue-Light.f10a0614.ttf") format("truetype");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Epilogue;
  src: local(Epilogue Regular), local(Epilogue-Regular), url("Epilogue-Regular.d60fe198.woff2") format("woff2"), url("Epilogue-Regular.a1c53fad.woff") format("woff"), url("Epilogue-Regular.14d956c1.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Epilogue;
  src: local(Epilogue SemiBold), local(Epilogue-SemiBold), url("Epilogue-SemiBold.a2908476.woff2") format("woff2"), url("Epilogue-SemiBold.1ec8ff40.woff") format("woff"), url("Epilogue-SemiBold.bc58743e.ttf") format("truetype");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

:root {
  --color-text: #fc4308;
  --accent: #fc4308;
  --bg-color: #313e2a;
  --bg-color-2: #0d1e20;
  --bg-color-3: #262d20;
  --white: #fff;
  --black: #000;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.no-scroll {
  overflow: hidden;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: var(--bg-color);
  color: var(--accent);
  font-family: Epilogue, sans-serif;
}

.header {
  padding: 20px;
  display: flex;
}

.container {
  width: 100%;
  justify-content: end;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header-nav {
  display: none;
}

.header-list {
  gap: 40px;
  display: flex;
}

.header-item {
  text-transform: uppercase;
  border: 1px solid var(--bg-color);
  box-shadow: inset 0 0 0 0 var(--accent);
  color: var(--color-text);
  border-radius: 15px;
  padding: 10px 2px;
  transition: color .3s ease-in-out, box-shadow .3s ease-in-out;
}

.header-item:hover {
  border: 1px solid var(--accent);
  color: var(--black);
  box-shadow: inset 200px 0 0 0 var(--accent);
  border-radius: 15px;
  transition-duration: 1s;
}

.header-link {
  padding: 20px 2px;
}

.mobile-menu-btn {
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.mobile-menu-pic {
  width: 32px;
  height: 32px;
  fill: var(--accent);
}

.mobile-menu-pic:hover {
  transform: scale(1.2);
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

